import React, { useState, useEffect } from "react";
import { useMediaQuery } from "react-responsive";
import BoxContainer from "../../component/BoxContainer";
import Title from "../../component/Title";
import styled, { css } from "styled-components";

const AllDataProtectionStatus = () => {
  const [blockedData, setBlockedData] = useState();

  useEffect(() => {
    fetch(
      "http://192.168.0.75:8080/ds_api/dashboard/statis/total/block-proc/all-group",
      { method: "POST" }
    )
      .then(res => res.json())
      .then(data => {
        setBlockedData(data.data);
      });
  }, [setBlockedData]);

  const todayProcessData =
    blockedData && blockedData[0].processBlockAllCnt.toLocaleString("en-US");
  const weekProcessData =
    blockedData && blockedData[1].processBlockAllCnt.toLocaleString("en-US");
  const monthProcessData =
    blockedData && blockedData[2].processBlockAllCnt.toLocaleString("en-US");

  const isPc = useMediaQuery({
    query: "(min-width:1240px)",
  });

  const isTablet = useMediaQuery({
    query: "(min-width:768px) and (max-width:1560px)",
  });

  return (
    <>
      {isPc ? (
        <BoxContainer title="    ">
          <Title title="    " />
          <FlexContainer>
            <Card line="none">
              <CardTextBox>
                <CountNum isTablet={isTablet}>{todayProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </Card>
            <Card>
              <CardTextBox>
                <CountNum isTablet={isTablet}>{weekProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </Card>
            <Card>
              <CardTextBox>
                <CountNum isTablet={isTablet}>{monthProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </Card>
          </FlexContainer>
        </BoxContainer>
      ) : (
        <BoxContainer>
          <Title title="    " />
          <FlexContainer>
            <TabletCard>
              <CardTextBox>
                <CountNum>{todayProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </TabletCard>
            <TabletCard>
              <CardTextBox>
                <CountNum>{weekProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </TabletCard>
            <TabletCard>
              <CardTextBox>
                <CountNum>{monthProcessData}</CountNum>
                <Date></Date>
              </CardTextBox>
            </TabletCard>
          </FlexContainer>
        </BoxContainer>
      )}
    </>
  );
};

export default AllDataProtectionStatus;

const FlexContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100% - 1.875rem);
  margin-right: 1rem;
  margin-left: -1rem;
`;

const Card = styled.div`
  display: flex;
  position: relative;
  width: 33%;
  height: 60%;
  text-align: center;
  color: #676a72;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    padding-right: -200px;
    height: 50px;
    border-right: 3px solid #676a72;
    vertical-align: middle;

    ${props =>
      props.line &&
      css`
        border: none;
      `}
  }
`;

const CardTextBox = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

const CountNum = styled.p`
  font-size: 2.6rem;
  font-weight: bold;

  ${props =>
    props.isTablet &&
    css`
      font-size: 2rem;
    `}
`;

const Date = styled.p`
  font-size: 1.3rem;
`;

const TabletCard = styled.div`
  position: relative;
  width: 33%;
  height: 100px;
  text-align: center;
  color: #676a72;
  cursor: pointer;
  ${props =>
    props.select &&
    css`
      color: #ffffff;
      background-color: #7e94d4;
    `}
`;
